import { Bullseye, Card, CardBody, Page, PageSection, PageSectionVariants, Spinner, Text, Title } from "@patternfly/react-core";
import { useEffect, useMemo, useState } from "react";
import { EmptyKey } from "../../Components/EmptyKey/EmptyKey";
import { Header } from "../../Components/Header/Header";
import { initContext } from "../../Components/OpenAIForm/ContextSetup/context/initContext";
import { OpenAiForm } from "../../Components/OpenAIForm/OpenAiForm";

export const OpenAIPage: React.FC<{}> = ({}) => {
  const [apiKey, setApiKey] = useState<string>();
  const [loading, setIsLoading] = useState<boolean>(false);

  useEffect(() => {
    if (apiKey) {
      setIsLoading(true);

      initContext(apiKey, (result) => {
        setIsLoading(false);
      });
    }
  }, [apiKey]);


  const BodyComponent = useMemo<JSX.Element>(() => {
    if (!apiKey) {
      return <EmptyKey setApiKey={setApiKey} />;
    }
  
    if (loading) {
      return (
        <Card isFlat isCompact isFullHeight>
      <CardBody>
        <Bullseye>
          <p>
            <Spinner isSVG size="xl" aria-label="Loading..." />
          </p>
          <p>
            <Title headingLevel="h4" size="lg">
              Loading...
            </Title>
          </p>
        </Bullseye>
        </CardBody>
        </Card>
      );
    }
  
    return <OpenAiForm apiKey={apiKey}/>;
  }, [apiKey, loading])

  
  return (
    <Page 
    header={<Header />}
    additionalGroupedContent={
      <PageSection variant={PageSectionVariants.light} isWidthLimited>
        <Text component="h1">OpenAI tests</Text>
        <Text component="p">Try generating a BPMN process using OpenAI!</Text>
      </PageSection>
    }
    >
      <PageSection isFilled style={{height: "760px"}}>
        {BodyComponent}
      </PageSection>
    </Page>
  )
};
