import { Configuration, OpenAIApi } from "openai";
import { ContextStep, ContextStepExecution } from "./api";
import { CONTEXT_STEPS } from "./steps";

export const initContext = (apiKey: string, consumer: (result: ContextStepExecution)=> void): void => {

    const openai = new OpenAIApi(
      new Configuration({
        apiKey: apiKey,
      })
    );

    CONTEXT_STEPS.forEach(async step => {
        const result = await initStep(openai, step);
        consumer(result);
    })
}

const initStep = async (openai:OpenAIApi, step: ContextStep): Promise<ContextStepExecution> => {

    try {
        const response = await openai.createCompletion({
            model: "text-davinci-003",
            prompt: step.prompt,
            temperature: 0.2,
            max_tokens: 3000,
          });

          let message;
          let success = false;

          if(response.data.choices.length > 0) {
            success = true;
            message = response.data.choices[0].text;
          }

          if(!message) {
            message= "Sorry, AI couldn't generate a valid response, try again...";
          }

          return {
            step,
                result: {
                    success: success,
                    message: message
                }
          }
    } catch(error: any) {
        return {
            step,
            result: {
                success: false,
                message: `Error with OpenAI API request: ${ error.response ? error.response.data.error.message : error.message }`,
            }
        }
    }
}